<template>
  <div class="module" v-if="myData">
    <h5 class="module-name">{{title}}</h5>
    <div class="chart">
      <div :id="chartId" style="width: 100%;height: 100%;"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "windowSix",
  props: {
    chartId: String,
    title: String,
    myData: Object,
  },
  watch: {
    "myData.company": {
      handler(val) {
        if (val) {
          this.$nextTick(() => {
            const top7 = val.slice(0, 7);
            const names = top7.map((v) => v.name);
            const datas = top7.map((v) => v.num);
            const rdatas = top7.map((v) => v.rnum);
            console.log(names, datas);
            this.setChart(names, datas, rdatas);
          });
        }
      },
      immediate: true,
    },
  },
  methods: {
    setChart(names, datas, rdatas) {
      const chart = this.$echarts.init(document.getElementById(this.chartId));
      const option = {
        color: [
          "#7DEE6F",
          {
            type: "linear",
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [
              {
                offset: 0,
                color: "#00CEFF", // 0% 处的颜色
              },
              {
                offset: 1,
                color: "#009AFC", // 100% 处的颜色
              },
            ],
            globalCoord: false, // 缺省为 false
          },
        ],
        legend:{
          type: 'plain',
          formatter:name=>{
            if(name == "分包数"){
              return `${name} ${this.myData.rnum}`
            } else {
              return `${name} ${this.myData.num}`
            }
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "6%",
          top: "15%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            data: names,
            axisLabel: {
              interval: 0, //横轴信息全部显示
              rotate: -30, //-30度角倾斜显示
              formatter: function(value) {
                return value.length > 4 ? value.slice(0, 3) + "..." : value;
              },
            },
            axisTick: {
              alignWithLabel: true,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
          },
        ],
        series: [
          {
            name: "分包数",
            type: "bar",
            barWidth: "60%",
            stack: "one",
            data: rdatas,
          },
          {
            name: "注册数",
            type: "bar",
            barWidth: "60%",
            stack: "one",
            data: datas,
          },
        ],
      };
      // console.log(chart);
      chart.setOption(option);
    },
  },
  mounted() {
    // console.log(this.$refs.p);
  },
};
</script>

<style scoped>
.title {
  display: flex;
  justify-content: space-between;
}
.title > .data {
  color: #000000;
  font-size: 18px;
  font-weight: bold;
}
.chart {
  position: relative;
  width: 100%;
  height: 90%;
}
.chart > .compare {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
