<template>
  <div
    class="module"
    v-if="myData"
    :style="{ '--leftColor': color[0], '--rightColor': color[1] }"
  >
    <div class="left">
      <h5 class="module-name">{{ title }}</h5>
      <div class="chat">
        <a-progress
          type="circle"
          :stroke-color="{
            '0%': '#00C4FF',
            '100%': '#009AFB',
          }"
          :width="windowSize.height * 0.14"
          :strokeWidth="10"
          :percent="myData.requestion_rate"
          :showInfo="false"
          status="normal"
          stroke-linecap="square"
          gapPosition="bottom"
        />
        <div class="data">
          <p class="member">{{ myData.dayrate }}<br /><span>日人均</span></p>
          <p class="member">
            {{ myData.requestion_rate }}%<br /><span>问题占比</span>
          </p>
        </div>
      </div>
      <div class="data">
        <p class="member">
          本月<span>{{ myData.allnum }}</span>
        </p>
        <p class="member">
          累计<span>{{ myData.leiji_m }}</span>
        </p>
      </div>
    </div>
    <div class="right">
      <ul class="top">
        <li
          :class="
            `name ${
              active.type == 'top5' && active.index == index ? 'active' : ''
            }`
          "
          v-for="(item, index) in myData.top5"
          :key="index"
          v-on:click="chouseModule('top5', index)"
        >
          <a-tooltip
            placement="topLeft"
            :title="item.name"
            arrow-point-at-center
          >
            <span>{{ index + 1 }}&emsp;{{ item.name }}</span>
          </a-tooltip>
        </li>
      </ul>
      <div class="member">
        <div class="title-box">
          <p class="title">前五名</p>
          <p class="title">后五名</p>
        </div>
        <div class="chart">
          <a-progress
            type="dashboard"
            :stroke-color="{
              '0%': color[0],
              '100%': color[1],
            }"
            :showInfo="false"
            :width="windowSize.height * 0.14"
            :strokeWidth="10"
            status="normal"
            :percent="myData[active.type][active.index].requestion_rate"
            :gapDegree="90"
            gapPosition="bottom"
          />
          <p class="title">
            {{ myData[active.type][active.index].requestion_rate }}%<span
              >问题占比</span
            >
          </p>
        </div>
        <div class="data">
          <p>
            {{ myData[active.type][active.index].dayrate }}<br /><span
              >日人均</span
            >
          </p>
          <p>
            {{ myData[active.type][active.index].allnum }}<br /><span
              >巡查数</span
            >
          </p>
          <p>
            {{ myData[active.type][active.index].requestion_num }}<br /><span
              >问题数</span
            >
          </p>
        </div>
      </div>
      <ul class="last">
        <li
          :class="
            `name ${
              active.type == 'end' && active.index == index ? 'active' : ''
            }`
          "
          v-for="(item, index) in myData.end"
          :key="index"
          v-on:click="chouseModule('end', index)"
        >
          <a-tooltip
            placement="topLeft"
            :title="item.name"
            arrow-point-at-center
          >
            <span>{{ index + 1 }}&emsp;{{ item.name }}</span>
          </a-tooltip>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "windowFour",
  props: {
    title: String,
    myData: Object,
    windowSize: Object,
    color: Array,
  },
  data() {
    return {
      active: {
        type: "top5",
        index: 0,
      },
    };
  },
  methods: {
    chouseModule(type, index) {
      this.active = { type, index };
    },
  },
};
</script>

<style scoped>
.module,
.right {
  display: flex;
  justify-content: space-between;
}
.left,
.right {
  height: 100%;
}
.left > .chat {
  display: flex;
  margin-top: 2vh;
}
.left > .chat > .data {
  margin-left: 2vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.left > .chat > .data > .member {
  font-size: 2.2vmin;
  color: #000000;
  font-weight: bold;
}
.left > .chat > .data > .member > span {
  font-size: 1.6vmin;
  color: rgba(0, 0, 0, 0.5);
}
.left > .data {
  display: flex;
  justify-content: space-between;
  margin-top: 2vh;
}
.left > .data > .member {
  font-size: 1.6vmin;
  color: rgba(0, 0, 0, 0.5);
}
.left > .data > .member > span {
  font-size: 2vmin;
  color: #000000;
  font-weight: bold;
  margin-left: 6px;
}
.right > .top > .name,
.right > .last > .name {
  color: rgba(0, 0, 0, 0.5);
  width: 5vw;
  height: 4vh;
  padding: 1vh 0.6vw;
  font-size: 1.4vmin;
  background-color: #f6f7f9;
  margin-top: 1vh;
  cursor: pointer;
  border-radius: 4px;
  position: relative;
}
.right > .top > .name > span,
.right > .last > .name > span {
  display: inline-block;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.right > .top > .name.active,
.right > .last > .name.active {
  background-color: var(--leftColor);
  background-image: linear-gradient(
    to right,
    var(--leftColor),
    var(--rightColor)
  );
  color: white;
}
.right > .top > .name.active::after,
.right > .last > .name.active::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 2vh;
  z-index: -1;
  top: 0;
  border-radius: 50%;
}
.right > .top > .name.active::after {
  background-color: var(--rightColor);
  right: -0.5vh;
}
.right > .last > .name.active::after {
  background-color: var(--leftColor);
  left: -1vh;
}
.right > .member {
  width: 14vw;
}
.right > .member > .title-box {
  margin-top: 1vh;
  font-size: 1.5vmin;
}
.right > .member > .title-box,
.right > .member > .data {
  display: flex;
  justify-content: space-between;
  padding: 0 1vw;
}
.right > .member > .chart {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.right > .member > .chart > .title {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 2.6vmin;
  font-weight: bold;
  line-height: 3vh;
  text-align: center;
  transform: translate(-50%, -50%);
}
.right > .member > .chart > .title > span {
  display: block;
  border-top: 1px solid #979797;
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.8vmin;
}
.right > .member > .data {
  text-align: center;
  color: #000000;
  font-size: 2vmin;
  font-weight: bold;
}
.right > .member > .data > p > span {
  font-size: 1.6vmin;
  font-weight: 400;
  /* margin-top: 1vh; */
  color: rgba(0, 0, 0, 0.5);
}
</style>
