<template>
  <div class="home">
    <my-header></my-header>
    <div class="window">
      <div class="left">
        <window-one :my-data="progress" title="进度管理" :window-size="windowSize"></window-one>
        <window-two :my-data="accept" title="质量验收" :window-size="windowSize"></window-two>
        <window-three :my-data="reform" title="项目整改" :window-size="windowSize"></window-three>
      </div>
      <div class="middle">
        <window-four :my-data="aq" title="安全" :window-size="windowSize" :color="['#FFB200','#FB8D00']"></window-four>
        <window-four :my-data="zl" title="技术质量" :window-size="windowSize" :color="['#00C4FF','#009AFB']"></window-four>
        <window-four :my-data="sc" title="生产" :window-size="windowSize" :color="['#44FFB1','#00B976']"></window-four>
      </div>
      <div class="right">
        <window-five
          chart-id="chart_1"
          :my-data="entry"
          title="材料验收"
        ></window-five>
        <window-five
          chart-id="chart_2"
          :my-data="checkin"
          title="进场管理"
        ></window-five>
        <window-six
          chart-id="chart_3"
          :my-data="register"
          title="分包注册"
        ></window-six>
      </div>
    </div>
    <!-- <Map></Map> -->
  </div>
</template>

<script>
// @ is an alias to /src
import myHeader from "@/components/myHeader";
import windowOne from "@/components/windowOne";
import windowTwo from "@/components/windowTwo";
import windowThree from "@/components/windowThree";
import windowFour from "@/components/windowFour";
import windowFive from "@/components/windowFive";
import windowSix from "@/components/windowSix";
export default {
  name: "Home",
  components: {
    myHeader,
    windowOne,
    windowTwo,
    windowThree,
    windowFour,
    windowFive,
    windowSix,
  },
  data() {
    return {
      progress: null,
      accept: null,
      reform: null,
      checkin: null,
      entry: null,
      register: null,
      sc: null,
      aq: null,
      zl: null,
      windowSize:{
        width: window.screen.availWidth,
        height: window.screen.availHeight
      }
    };
  },
  methods: {
  },
  mounted() {
    const axios = this.$axios;
    const params = {
      date: "2020-08",
      company_id: "list",
    };
    const http = [
      {
        api: "/api/temporary/specs/progress",
        target: "progress",
      },
      {
        api: "/api/temporary/specs/accept",
        target: "accept",
      },
      {
        api: "/api/temporary/specs/reform",
        target: "reform",
      },
      {
        api: "/api/temporary/specs/checkin",
        target: "checkin",
      },
      {
        api: "/api/temporary/specs/entry",
        target: "entry",
      },
      {
        api: "/api/temporary/specs/register",
        target: "register",
      },
      {
        api: "/api/temporary/specs/sc",
        target: "sc",
      },
      {
        api: "/api/temporary/specs/aq",
        target: "aq",
      },
      {
        api: "/api/temporary/specs/zl",
        target: "zl",
      },
    ];
    for (const iterator of http) {
      axios
        .get(iterator.api, {
          params,
        })
        .then((res) => {
          console.log(res);
          const { code, data } = res.data;
          if (code == 0) {
            this[iterator.target] = data;
          }
        });
    }
  },
};
</script>
