<template>
  <div class="box">
    <div class="member" v-for="item in topFive" :key="item.id">
      <p class="name">{{item.name}}</p>
      <p class="grade">{{item.done_rate}}%</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "horizontalRank",
  props: {
    rank: Array,
  },
  watch: {
    rank: {
      handler() {
        if (this.rank.length > 0) {
          this.topFive = this.rank.slice(0, 5);
        }
      },
      immediate: true,
    },
  },
  data() {
    return {
      topFive: [],
    };
  },
};
</script>

<style scoped>
.box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.member {
  width: 20%;
  text-align: center;
}
.name {
  font-size: 2vmin;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: rgba(0, 0, 0, 0.85);
}
.grade {
  font-size: 1.8vmin;
  color: rgba(0, 0, 0, 0.5);
}
</style>
